import React, { useCallback, useEffect, useState } from 'react';
import * as T from './types';
import * as S from './styles';
import { colors } from '../../shared';

// console.log(colors.primary[100]);
let colorsObject = [{}];

const keys = Object.keys(colors);
keys.map(key => {
  const variants = Object.keys(colors[key]);
  const hex = variants.map(variant => {
    const hex = colors[key][variant];
    return hex;
    // console.log(hex);
    // colorsObject = [...colorsObject, { key }];
    //   colorsObject = [...colorsObject, { key, variants, hex }];
  });
  colorsObject = [...colorsObject, { key, variants: [hex] }];
});

export const Colors: React.FC<T.ColorsProps> = () => {
  console.log(colorsObject);
  return (
    <S.Wrapper>
      {colorsObject.length &&
        colorsObject.map((color, index) => <div key={index}>{color.key}</div>)}
    </S.Wrapper>
  );
};
